
















* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
html {
	font-size: 18px;
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
.container {
	width: 100%;
	padding: 0 20px;
	text-align: center;
	margin: 0 auto;
}
table {
	margin: 0 auto;
	border-collapse: collapse;
}
th {
	background: #ccc;
	font-size: 18px;
}
tr {
	border: 1px solid #000;
}
tr:nth-child(2n) {
	background: #ccc;
}
